
@charset "UTF-8";
body {
  background: #EEEEEE;
  font-family: '微软雅黑';
}
.topH {
  margin-top: 7.3rem;
}
#firstArea {
  width: 100%;
  padding: 20px 0px;
  text-align: left;
  /*border-bottom:1px solid rgba(0,0,0,.3);*/
}
.bookName {
  font-size: 2.1rem;
  text-indent: 1rem;
  color: rgba(0, 0, 0, 0.5);
  padding-bottom: 0.3rem;
}
#usernameC {
  font-size: 2.2rem;
  text-indent: 1rem;
  padding: 0.3rem 0rem;
  color: #1b1b1b;
  font-weight: bolder;
}
#bookArea {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  /*background:white;*/
  padding: 20px 20px 40px 35px;
  /*border-bottom:2px solid rgba(0,0,0,.3);*/
}
#bookArea span {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  direction: row;
  width: 100%;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
#content #firstArea #xuebaSpan {
  height: 40px;
  font-size: 40px;
  line-height: 40px;
  margin-left: 40px;
  padding-left: 20px;
  border-left: 10px solid #70A595;
}
#firstArea span.right {
  display: inline-block;
  text-align: right;
  position: absolute;
  right: 30px;
  padding-top: 4px;
  font-size: 35px;
  font-weight: bolder;
  color: #70A595;
}
.titleC1 {
  float: left;
  border: 2px solid #eeeeee;
  font-size: 36px;
  text-align: left;
  border-radius: 8px;
  background-color: #ffffff;
}
#bookArea div #imgDiv {
  /*display: none;*/
  width: 14rem;
  height: 11rem;
  overflow: hidden;
  text-align: center;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  /*padding:5px;*/
}
.titleC1 #bookImg {
  display: inline-block;
  text-align: center;
  width: 14rem;
  /*height: auto;*/
  /*height: 210px;*/
}
#active {
  margin-top: 500px;
  width: 100%;
}
#tab-container {
  position: relative;
  /*margin-top: 2rem;*/
  /*top:2rem;*/
  background: #FAFAFA;
}
#tab-container #publicActive {
  display: inline-block;
  position: absolute;
  text-decoration: none;
  width: 250px;
  height: 100px;
  line-height: 100px;
  font-size: 40px;
  text-align: center;
  right: 22px;
  /*    top: 0px;*/
  color: white;
  z-index: 2;
  background: #1A9FFF;
  font-weight: bolder;
  border: 1px solid rgba(0, 0, 0, 0.3);
  box-shadow: 0px 1px 50px 10px rgba(0, 0, 0, 0.3);
}
#tab-container #tab-nav {
  /*background-color: #1Abc9C;*/
  border-top: 2px solid #EEEEEE;
  border-bottom: 1px solid #EEEEEE;
  /*box-shadow: 0px 1px 0px 2px rgba(0,0,0,.3);*/
  background: white;
  color: rgba(0, 0, 0, 0.5);
  font-weight: bolder;
}
#tab-container #tab-nav li {
  position: relative;
}
#tab-container #tab-nav .bottomLine1 {
  width: 60%;
  height: 0.5rem;
  background-color: #70A595;
  display: inline-block;
  position: absolute;
  left: 50%;
  margin-left: -30%;
  bottom: 0;
}
#tab-container #tab-nav li {
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  list-style: none;
  width: 25%;
  height: 100px;
  line-height: 100px;
  font-size: 36px;
  margin-bottom: 0px;
  text-align: center;
}
#tab-container #tab-nav li a {
  display: inline-block;
  height: 60px;
  line-height: 60px;
  margin: auto 0px;
  width: 100%;
  color: rgba(0, 0, 0, 0.5);
}
#tab-container #tab-nav li:first-child a {
  border-right: 4px solid #eeeeee;
}
.tab-content {
  width: 100%;
  margin: 3px auto;
  background: white;
}
.font40 {
  font-size: 40px;
}
#tab-container #tab-nav li .activeEffect {
  color: #70A595;
  /*background: #1A9FFF;*/
  padding-bottom: 0px;
  /*border-bottom: 4px solid #444444;*/
}



