
#aritcle_foot_guide{
  background-color: #fff;
  position: fixed;
  z-index: 100;
  left: 0;
  right: 0;
  bottom: 0;
  height: 110px;
  font-size: 2rem;
  color: #ffffff;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  background: #666666;
}
.aritcle_guide_item{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: space-evenly;
      -ms-flex-pack: space-evenly;
          justify-content: space-evenly;
}
.guide_img{
  margin: auto;
  width: 10rem;
  height: 100%;
  background: no-repeat center;
  /*background-size:45% 65%;*/
}
.aritcle_guide_item span{
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.icon_style{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.icon_style div{
  margin-bottom: .2rem;
  margin-top: .3rem;
  width: 2rem;
  height: 2rem;
  background-color: #1a9fff;
}
/*目录*/
.over{
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: rgba(0,0,0,0.5);
}
.aritcle_categories_ul{
  position: absolute;
  background: #ffffff;
  bottom: 110px
}
.aritcle_categories_li{
  font-size: 2.5rem;
  color: #000000;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  /*justify-content: center;*/
  border-top: 1px solid #e1e1e1;
}
.aritcle_categories_li span {
  padding: 0 2rem;
  /*text-align: center;*/
  line-height: 7.7rem;
}
/* over动画*/
.bounce-enter-active {
  -webkit-animation: bounce-in .5s;
          animation: bounce-in .5s;
}
.bounce-leave-active {
  -webkit-animation: bounce-in .5s reverse;
          animation: bounce-in .5s reverse;
}
@-webkit-keyframes bounce-in {
0% {
   opacity: 0;
    left: -30rem;
}
  /*50% {*/
    /*transform:rotate(0deg);*/
  /*}*/
100% {
    /*transform:rotate(360deg);*/
    opacity: 1;
    left: 0px;
}
}
@keyframes bounce-in {
0% {
   opacity: 0;
    left: -30rem;
}
  /*50% {*/
    /*transform:rotate(0deg);*/
  /*}*/
100% {
    /*transform:rotate(360deg);*/
    opacity: 1;
    left: 0px;
}
}



