
.content{
    margin-top: 117px;
    /*font-size: 30px;*/
}
.content div ul li{
    display: -webkit-inline-box;
    display: -ms-inline-flexbox;
    display: inline-flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    list-style: none;
    height: auto;
    width: 920px;
    line-height: 100px;
    font-size: 36px;
    margin-bottom: 0px;
    text-align: center;
    background: white;
    border-bottom: 4px solid #EEEEEE;
}
.content div ul li .collect-left-bookImg{
  width: auto;
  height: auto;
  margin: 1rem;
  box-shadow: 3px 3px 3px 3px #000000;
}
.content div ul li .collect-left-bookImg img{
  width: 14.8rem;
  height: 17.8rem;
}
.content div ul li .collect-right{
  width: 100%;
}
.content div ul li .collect-right .collect-right-container{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 100%;
  text-align: left;
  padding: 0px 20px;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.content div ul li .collect-right .collect-right-top{
  width: auto;
}
.content div ul li .collect-right .collect-right-bottom{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.content div ul li .collect-right .collect-right-bottom .collect-right-bottom-right{
  border: 1px solid black;
  border-radius: 5px;
  color: #1A9FFF;
  height: 60px;
  line-height: 63px;
  width: 210px;
  margin-top: 20px;
  text-align: center;
  /*background: url('./../../../assets/GeneralIcon/Trash Can.png') no-repeat center left;*/
  background-size:3.5rem;
  text-indent: 3rem;
}
#noCollectPannel{
  text-align: center;
  font-size: 40px;
  height: auto;
  line-height: auto;
}



