
#box{
  	text-align: center;
  	font-size: 40px;
}
#box ul li{
	height: 100%;
	width: 100%;
	margin: 0px;
	overflow: hidden;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: horizontal;
	-webkit-box-direction: normal;
	    -ms-flex-direction: row;
	        flex-direction: row;
	-webkit-box-pack: justify;
	    -ms-flex-pack: justify;
	        justify-content: space-between;
	border-bottom: 1px solid #EEEEEE;
}
#box ul li:nth-child(2n+1){
	/*background-color: rgba(0,255,255,.05)*/
}
#pgA-area1{
	float: left;
	width:200px;
}
.imgSize150{
		margin-top: 1rem;
		width:7.5rem;
		height:7.5rem;
		border-radius: 50%;
}
#pgA-area2{
	width:98%;
	text-align: left;
}
#pgA-area2 .one{
 	position: relative;
	padding-top: 1rem;
}
#pgA-area2 .two{
 	position: relative;
	padding-top: 1rem;
  padding-right: 1.5rem;
  font-size: 2.2rem;
  color: #1b1b1b;
}
#pgA-area2 .three{
 	position: relative;
	padding: 1rem 2rem;
  overflow: hidden;
}
#pgA-area2 span.right0{
  display:inline-block;
  text-align: right;
  position: absolute;
  right: 30px;
  height: 57px;
  line-height:57px;
  color: rgba(0,0,0,.5);
}
.fontSize2p7{
	font-size: 2.3rem;
}
.fontSize2p{
	font-size: 32px;
}
.mainColor{
    color: #70A595;
}
#pgA-area2 .three span{
    display: block;
    float: right;
    position: relative;
    height: 57px;
    line-height:57px;
}
#chatIcon{
  display: inline-block;
  width: 60px;
  height: 57px;
  line-height:57px;
  /*background: url("./../../assets/GeneralIcon/Chat.png") -10px -10px no-repeat;*/
  background-size: 4.5rem;
}
#thumbsIcon{
  display: inline-block;
  width: 60px;
  height: 57px;
  /*background: url("./../../assets/GeneralIcon/thumbs.png") -10px -10px no-repeat;*/
  background-size: 4.5rem;
  margin-left:2rem;
}
#footerHolder{
  	height: 160px;
}




