
#settingDiv {
    font-size:36px;
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    margin-top: 115px;
    background: #EEEEEE;
    background: white;
    width: 96%;
    margin:115px auto;
    box-shadow: 0px 3px 3px #eeeeee;
}
#content #settingDiv{
  background: white;
  margin-top: 30px;
  margin-bottom: 300px;
  box-shadow: 0px 3px 3px #eeeeee;
}
#settingDiv ul li{
  display:block;
  height: 120px;
  border-bottom:1px solid #eeeeee;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
#settingDiv ul li:hover{
  background-color: #C7C7C7;
}
#settingDiv ul li span.item1{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: 60px;
  height: 120px;
}
#settingDiv ul li span.item2{
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  text-align: left;
  width: 220px;
  height: 120px;
  line-height: 120px;
}
#settingDiv ul li span.item3{
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: 120px;
  height: 120px;
  line-height: 120px;
  margin-left: 350px; 
  margin-right: 50px;
}
#settingDiv ul li span.item4{
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: 60px;
  height: 120px;
  line-height: 120px;
}
#settingDiv ul li span.item1 img{
  width:60px;
  height:60px;
}



