
.long_Press_over{
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: rgba(0,0,0,0.5);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.long_Press_tool{
  margin: auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  background: #000000;
  border-radius: 1rem;
  color: #ffffff;
}
.long_Press_tool p{
  /*border: solid 1px #ffffff;*/
  margin: 2rem;
}
.articleTemp_top{
  display: -webkit-box;
  display: -ms-flexbox;
