  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
}
.keep_eye_box{
  margin: 2rem;
}
.article{
  -webkit-touch-callout:none;
  -webkit-user-select:none;
  -moz-user-select:none;
