/* 上面是为了保证滑动的时候不出现抖动情况 */
.child-view[data-v-530d0a5c] {
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  transition: all 0.5s cubic-bezier(0.55, 0, 0.1, 1);
  background-color: #f2f2f2;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}

/* 当child-view的内容过多时会撑开child-view使得内部能够滚动 */
.slide-left-enter[data-v-530d0a5c], .slide-right-leave-active[data-v-530d0a5c] {
  opacity: 0;
  -webkit-transform: translate(750px, 0);
  transform: translate(750px, 0);
  transition-delay: .5s;
  -webkit-transition-delay: .5s;
}
.slide-left-leave-active[data-v-530d0a5c], .slide-right-enter[data-v-530d0a5c] {
  opacity: 0;
  -webkit-transform: translate(-750px, 0);
  transform: translate(-750px, 0);
  transition-delay: .5s;
  -webkit-transition-delay: .5s;
}
.slide-enter-active[data-v-530d0a5c] {
  transition: all .3s ease;
}
.slide-leave-active[data-v-530d0a5c] {
  transition: all 0.8s cubic-bezier(1, 0.5, 0.8, 1);
}

/* 然后写上切换时候的类名的CSS变化(这里最好看一下VUE的transition文档) */



