
.form1{
    padding:30px ;
    font-size:30px;
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #2c3e50;
    margin-top: 180px;
    text-align: center;
}
.form1 p:first-child{
  padding: 0 1rem;
  border:1px solid #70A595;
  border-radius: 8px;
}
.form1 input{
  font-size:2.5rem;
  width: 100%;
  height: 6rem;
  border: none;
}
.form1 .okBtn{
    margin:50px auto;
    font-size:2.5rem;
    width: 100%;
    background-color: #70A595;
    border-radius: 8px;
    border: 0px;
    height: 6rem;
    color: white;
    font-family: "微软雅黑"
}



