
html{
      background-color: #FAFAFA;
}
#content {
    font-size:2.4rem;
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    background-color: #FAFAFA;
/*    margin-top: 115px;*/
}
#content #content-title{
  width: 100%;
  height: 100%;
  /*background: url(./../../assets/my/mybgm.png) center center no-repeat;*/
  background-size: 100% 100%;
  padding-top: 4rem;
  padding-bottom: 1rem;
  color: white;
}
#content #content-main{
 /*padding: 30px;*/
}
#whiteRadius{
  position: relative;
  display: inline-block;
  width: 220px;
  height: 220px;
  background-color: white;
  border-radius: 50%;
  /*text-align: center;*/
}
#myInfoEditIcon{
  display: inline-block;
  position: absolute;
  right: -3rem;
  top: 1rem;
  width: 5rem;
  height: 5rem;
}
.imgSize200{
  border-radius: 50%;
  width: 200px;
  height: 200px;
  margin-top:10px;
}
.imgSize100{
  border-radius: 50%;
  width: 120px;
  height: 120px;
}
.qiandao{
  height: 145px;
  width: 20rem;
  font-size: 2.5rem;
}
.qiandao .bg1{
  position: relative;
  display: inline-block;
  background: url('./../../assets/GeneralIcon/Sign.png') 10rem 0rem no-repeat;
  /*background-size: 25% 50%;*/
  color: white;
  right: 0px;
  top: -3rem;
}
.qiandao .bg2{
  position: relative;
  display: inline-block;
  background-color: #BFBFBF;
  color: white;
  text-indent: -1.8em;
  text-align: center;
  border-radius: 50px;
  width: 250px;
  height: 80px;
  padding-top: 0.6rem;
  right: 2rem;
  top: 2rem
}
.qiandao span{
  position: relative;
  display: inline-block;
  background-color: #70A595;
  color: white;
  text-indent: -1.8em;
  text-align: center;
  border-radius: 50px;
  width: 250px;
  height: 80px;
  padding-top: 0.6rem;
  right: 2rem;
  top: 2rem
}
.qiandao input{
  display: inline-block;
  position: absolute;
  top: -5px;
  right: -50px;
  border: 1px solid black;
  width: 50px;
  height: 50px;
  border-radius: 5px;
}
.text-center{
  margin: 0 0;
  padding-bottom: 30px;
  color: #2a5a72;
  font-weight: bolder;
}
.description2{
  /*color: #333333;*/
  margin: 0 0;
  padding-bottom: 30px;
  margin-top: 10px;
  font-size: 2.2rem;
  line-height: 32px;
  color: #37909d;
}
.description2 #localtionIcon{
  position: relative;
  top: 0.5rem;
  display: inline-block;
  width: 2rem;
  height: 2.5rem;
  line-height: 2.5rem;
}
#content table,tr, td{
  table-layout:fixed;
  border-collapse: collapse;
  font-size: 2.2rem;
}
.last1{
  border-bottom: 1px solid rgba(0,0,0,0);
}
#content table{
  position: relative;
  left:2.6rem;
  right: 13rem;
  width:100%;
  height:8rem;
}
#content table tr td.first{
  width:10%;
}
#content table tr td.second{
  width:10%;
  color: #70A595;
}
#content table tr td.three{
  width:10%;
}
#content table tr td.four{
  width:10%;
  color: #70A595;
}
#content table tr td.five{
  width:90%;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
}
.last2 input{
    position: absolute;
    top: 626px;
    right: 100px;
    margin:50px auto;
    font-size:3.5rem;
    width: 300px;
    background-color: #6699ff;
    border-radius: 10px;
    border: 0px;
    height: 80px;
    color: white;
    font-family: "微软雅黑"
}
#content #menuDiv{
  background: white;
  /*margin-top: 30px;*/
  margin-bottom: 300px;
  /*box-shadow: 0px 3px 3px #999999;*/
}
#content ul li{
  display:block;
  height: 10rem;
  text-align: center;
  border-bottom:2px solid #F4F4F4;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
#content ul li span.item1{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: 60px;
  height: 10rem;
  margin-left: 2rem;
}
#content ul li span.item2{
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: 200px;
  height: 10rem;
  font-size: 2.5rem;
  line-height: 10rem;
}
#content ul li span.item3{
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: 80px;
  height: 10rem;
  line-height: 10rem;
  margin-left: 500px;
}
#content ul li span.item4{
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: 60px;
  height: 10rem;
  color: #70A595;
  font-weight: bolder;
  line-height: 10rem;
  margin-right: 1rem;
}
#content ul li span.item1 img{
  width:3.5rem;
  height:3.5rem;
}
#content ul li span.item4 img{
  padding-top: 3.5rem;
  text-align: center;
  /*width:2rem;
  height:2rem;*/
}



